// CarbonFlow API Types

export interface OrchestratorStatus {
  status: "operational" | "idle" | "inactive"
  last_run: string
  cycle_duration_seconds: number
  agents: {
    sensor_ingest: AgentStatus
    forecast: AgentStatus
  }
}

export interface AgentStatus {
  last_run: string | null
  status: "available" | "unavailable" | "running"
  message?: string
}

export interface DataSource {
  cpcb_aqi: number
  nasa_fire_count: number
  stubble_burning_percent: number
  avg_wind_direction_24h_deg: number
  avg_wind_speed_24h_kmh: number
}

export interface ForecastPrediction {
  aqi_category: "Good" | "Satisfactory" | "Moderate" | "Poor" | "Very Poor" | "Severe"
  confidence_level: number
  reasoning: string
  data_sources: DataSource
  timestamp: string
  predicted_aqi: number
  eta_hours: number
}

export interface ForecastLatest {
  prediction: ForecastPrediction
  artifacts: string[]
}

export interface CPCBStation {
  station: string
  aqi: number
  pm25: number
  pm10: number
  lat: number
  lon: number
  timestamp: string
  category: string
}

export interface NASAFireData {
  lat: number
  lon: number
  brightness: number
  confidence: number
  timestamp: string
}

export interface DSSData {
  stubble_burning_percent: number
  affected_area_km2: number
  timestamp: string
}

export interface SensorData {
  cpcb_data: CPCBStation[]
  nasa_data: NASAFireData[]
  dss_data: DSSData
}

export interface LogEntry {
  timestamp: string
  level: "info" | "warning" | "error" | "success"
  message: string
}

export interface AgentHistoryEntry {
  timestamp: string
  status: "success" | "failure" | "running" | "pending"
  message: string
}

export interface AgentHistory {
  sensor_ingest: AgentHistoryEntry[]
  forecast: AgentHistoryEntry[]
}

export interface ForecastHistoryEntry {
  timestamp: string
  aqi: number
  fire_count: number
  stubble_percent: number
}

// AQI category utilities
export const AQI_CATEGORIES = {
  Good: { min: 0, max: 50, color: "#22c55e", bg: "bg-green-500" },
  Satisfactory: { min: 51, max: 100, color: "#84cc16", bg: "bg-lime-500" },
  Moderate: { min: 101, max: 200, color: "#eab308", bg: "bg-yellow-500" },
  Poor: { min: 201, max: 300, color: "#f97316", bg: "bg-orange-500" },
  "Very Poor": { min: 301, max: 400, color: "#ef4444", bg: "bg-red-500" },
  Severe: { min: 401, max: 500, color: "#b91c1c", bg: "bg-red-700" },
} as const

export type AQICategory = keyof typeof AQI_CATEGORIES

export function getAQICategory(aqi: number): AQICategory {
  if (aqi <= 50) return "Good"
  if (aqi <= 100) return "Satisfactory"
  if (aqi <= 200) return "Moderate"
  if (aqi <= 300) return "Poor"
  if (aqi <= 400) return "Very Poor"
  return "Severe"
}

export function getAQIColor(aqi: number): string {
  const category = getAQICategory(aqi)
  return AQI_CATEGORIES[category].color
}
