import type {
  OrchestratorStatus,
  ForecastLatest,
  SensorData,
  LogEntry,
  AgentHistory,
  ForecastHistoryEntry,
} from "./types"

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "http://localhost:8000"

async function fetchAPI<T>(endpoint: string, options?: RequestInit): Promise<T> {
  const response = await fetch(`${API_BASE_URL}${endpoint}`, {
    ...options,
    headers: {
      "Content-Type": "application/json",
      ...options?.headers,
    },
  })

  if (!response.ok) {
    throw new Error(`API Error: ${response.status} ${response.statusText}`)
  }

  return response.json()
}

// GET endpoints
export async function getStatus(): Promise<OrchestratorStatus> {
  return fetchAPI<OrchestratorStatus>("/api/status")
}

export async function getForecastLatest(): Promise<ForecastLatest> {
  return fetchAPI<ForecastLatest>("/api/forecast/latest")
}

export async function getSensorsLatest(): Promise<SensorData> {
  return fetchAPI<SensorData>("/api/sensors/latest")
}

export async function getRecentLogs(limit = 100): Promise<{ logs: LogEntry[] }> {
  return fetchAPI<{ logs: LogEntry[] }>(`/api/logs/recent?limit=${limit}`)
}

export async function getAgentHistory(): Promise<AgentHistory> {
  return fetchAPI<AgentHistory>("/api/agents/history")
}

export async function getForecastHistory(days = 7): Promise<ForecastHistoryEntry[]> {
  return fetchAPI<ForecastHistoryEntry[]>(`/api/forecast/history?days=${days}`)
}

// POST endpoints (actions)
export async function runForecastCycle(): Promise<{ success: boolean; message: string }> {
  return fetchAPI<{ success: boolean; message: string }>("/api/agents/forecast/run", {
    method: "POST",
  })
}

